import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'gatsby';
import posed from 'react-pose';
import Nav from 'components/productHeader/nav';
import Icon from 'images/tov_logo_light.png';
import { Container } from './productHeader.css';

// Example of a component-specific page transition
const AnimatedContainer = posed.div({
    enter: {
        y: 0,
        transition: {
            ease: 'easeInOut',
        },
    },
    exit: {
        y: '-100%',
        transition: {
            ease: 'easeInOut',
        },
    },
});

const ProductHeader = ({ altImage }) => (
    <AnimatedContainer>
        <Container>
            <Link to="/">
                <img height="44" src={Icon} alt={altImage} />
            </Link>

            <Nav />
        </Container>
    </AnimatedContainer>
);

ProductHeader.propTypes = {
    altImage: PropTypes.string.isRequired,
};

export default ProductHeader;
